/*****************************************************************************/
/*
/* Tufte Jekyll blog theme
/* Based on Tufte CSS by Dave Liepmann ( https://github.com/edwardtufte/tufte-css )
/*
/* The README.md will show you how to set up your site along with other goodies
/*****************************************************************************/

// Imports to create final

@import "../_sass/fonts";
@import "../_sass/settings";
@import "../_sass/syntax-highlighting";


// Global Resets

* { margin: 0; padding: 0; }

// START MountAye's addition:

.important, strong {color: $important-color;} // !important
.postContent p {text-indent: 2em;}
.postContent h1,
.postContent h2,
.postContent h3 { color: $contrast-color; }
// .postContent h1::before { font-family: sans-serif;
//                           content:"\00023\00a0"; }
.postContent h2::before { font-family: sans-serif;
                          content:"\00023\00a0"; }
.postContent h3::before { font-family: sans-serif;
                          content:"\00023\00023\00a0"; }
.postContent ul,
.postContent ol {padding-left: 2em;}
.postContent blockquote { margin-left: 2em;
                          margin-top: 2em}
.postContent blockquote p { text-indent: 2em;
                            margin: 0.5em 0em;}
.postContent a { font-weight: bold }
.postContent a:hover { text-decoration: underline }

.postContent > blockquote, .postContent .highlighter-rouge, .postContent > p, .postContent > footer, .postContent > div.table-wrapper, .postContent > div.mathblock{ width: 65%; }

blockquote {
  background-color: #fffff0;
  border-left: solid thick $contrast-color;
  padding: 1em 0em; 
}
.highlighter-rouge {
  color: $important-color;
  background-color: #fffff0;
  padding: 0em 0.5em
}

.ghComment { margin-left: 65px; }
.ghIssueTitle { margin-bottom: 0.2em; }
.ghIssueTitle::before { font-family: sans-serif;
                        content:"\00023\00a0"; }
.ghHeader { display: table;
            width: 100%; }
.ghIssue .ghHeader { border-left: solid thick $contrast-color; }
.ghComment .ghHeader { border-left: solid thick $important-color; }
.ghLogo { display: table-cell;
          vertical-align: middle;
          width: 60px;
          padding-left: 5px;}
.ghLogo a { vertical-align: middle; }
.ghAuthor { display: table-cell;
            vertical-align: middle;}
.ghAuthor p { margin:0;
              text-indent: 0; }
.ghBody {border-left: solid thick gray;}
.ghBody .highlight { margin-right: 1em; }
.ghFooter { background-color: #f8f8f8;
            border-left: solid thick $contrast-color;
            padding: 0.5em 0em; }
.ghFooterBottomRight { margin-right: 0.5em;
                       float: right; }
.ghBody, .ghFooter { width: inherit;
                     margin-left: 65px; 
                     padding-left:1em; }
.ghBody h1 { font-size:1.5em; }
.ghBody h2 { font-size:1.3em; }
.ghBody h3 { font-size:1.1em; }
.ghBody p  { font-size:1em; }
.ghBody ul { font-size:1em; }
.ghBody ol { font-size: 1em; }
.ghBody table { font-size:1em;
                padding-left: 2em; }
.ghBody h1::before {  font-family: sans-serif;
                      content:"\00023\00a0";}
.ghBody h2::before {  font-family: sans-serif;
                      content:"\00023\00023\00a0";}
.ghBody h3::before {  font-family: sans-serif;
                      content:"\00023\00023\00023\00a0";}

// END MountAye's addition

/* clearfix hack after Cederholm (group class name) */
.group:after {
  content: "";
  display: table;
  clear: both;
}

html, body { height: 100%; }

// First significant deviation from CSS on tufte.css site - variable font size as browser width expands or contracts
//html { font-size: 15px; }

html{
    text-align: baseline;
    font-size: 11px;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;

  }

@media screen and (min-width: 800px){ html{ font-size: 12px;} }

@media screen and (min-width: 900px){ html{ font-size: 13px;} }

@media screen and (min-width: 1000px){ html{ font-size: 14px;} }

// @media screen and (min-width: 1100px){ html{ font-size: 15px; } }

// @media screen and (min-width: 1200px){ html{ font-size: 16px; } }

// @media screen and (min-width: 1300px){ html{ font-size: 17px; } }


// sets link style according to values in _settings.scss
.mathblock{
  font-size: 1.3rem;
}
a {
  color: $contrast-color;
  text-decoration: none;
}

p > a { @if $link-style == underline
    {
      color: $text-color;
      text-decoration: none;
      border-bottom: 1px solid #777;
      padding-bottom: 1px;
    }
    @else
    {
      color: $contrast-color;
      text-decoration: none;
    }
  }

body { width: 90%;
       margin-left: auto;
       margin-right: auto;
       font-family: et-bembo, Palatino, "Palatino Linotype", "Palatino LT STD", "Book Antiqua", Georgia, NSimSun, STSong, serif;
       background-color: $bg-color;
       color: $text-color;
       max-width: 1400px;
       counter-reset: sidenote-counter; }

// --------- Typography stuff -----------//
// added rational line height and margins ala http://webtypography.net/intro/

h1 { font-weight: bold;
     margin-top: 1.5em;
     margin-bottom: 0.3em;
     font-size: 2.0em;
     line-height: 1.0em; }

h2 { font-style: bold;
     font-weight: bold;
     margin-top: 1.5em;
     margin-bottom: 0;
     font-size: 1.8em;
     line-height: 0.933333333333333; }

h3 { font-style: bold;
     font-weight: bold;
     font-size: 1.5em;
     margin-top: 1em;
     margin-bottom: 0;
     line-height: 1.08888888888889; }

// ET says a need for more than 3 levels of headings is the sign of a diseased mind

p.subtitle { font-style: italic;
             margin-top: 0.6em;
             margin-bottom: 1.2em;
             font-size: 1.4em;
             display: block;
             line-height: 1.08888888888889; }


p, ol, ul { font-size: 1.4rem; }

p, li { line-height: 1.5em;
        margin-top: 1.1rem; //modified by MountAye
        padding-right: 2rem; //removed because, why?
        vertical-align: baseline; }


blockquote p,
blockquote ol,
blockquote ul {  font-size: 1.5em;
                line-height: 1.5em;
                margin-top: 0.5em;
                margin-bottom: 0.5em;
                width: 95%;  // modified by MountAye
                padding-left: 2.5%;
                padding-right: 2.5%; }

blockquote footer { width: 65%;
                    text-align: right; }


.sans { font-family: $sans-font;
        letter-spacing: .03em; }

pre, pre code, p code, p pre code { font-family: $code-font;  // removed .code 'class' since code is an actual html tag
        font-size: 1.2rem;                   // also added p code, p pre code and pre selector to account for Markdown parsing
        line-height: 1.71428571;        // of triple backticks plus rationalized line-heights and margins
        margin-top: 1.71428571rem; }


h1 code, h2 code, h3 code { font-size: 0.80em; } //left in for no real reason

/*-- Table styling section - For Tufte-Jekyll, booktabs style is default for Markdown tables  --*/

table, table.booktabs { width: auto;  //making booktabs style tables the unstyled default in case someone uses Markdown styling
                 margin: 0 auto;
                 border-spacing: 0px;
                 border-top: 2px solid $border-color;
                 border-bottom: 2px solid $border-color; }

.booktabs th, th { border-bottom: 1px solid $border-color;
               padding: 0.65ex 0.5em 0.4ex 0.5em;
               font-weight: normal;
               text-align: center; }

th, td{ font-size: 1.2rem;
        line-height: 1.71428571;  }

.booktabs th.cmid, th { border-bottom: 1px solid lighten($border-color, 25%); }

.booktabs th.nocmid { border-bottom: none; }

.booktabs tbody tr:first-child td,  tr:first-child td { padding-top: 0.65ex; } /* add space between thead row and tbody */

.booktabs td, td {  padding-left: 0.5em;
                    padding-right: 0.5em;
                    text-align: left; }

.booktabs caption, caption { font-size: 90%;
                    text-align: left;
                    width: auto;
                    margin-left: auto;
                    margin-right: auto;
                    margin-top: 1ex;
                    caption-side: top; }

.booktabs tbody tr td.l { text-align: left !important; }
.booktabs tbody tr td.c { text-align: center !important; }
.booktabs tbody tr td.r { text-align: right !important; }

.table-caption { float:right;
                 clear:right;
                 margin-right: -60%;
                 width: 50%;
                 margin-top: 0;
                 margin-bottom: 0;
                 font-size: 1.0rem;
                 line-height: 1.96; }
/* -- End of Table Styling section --*/


/* Basic Layout stuff --*/

article { position: relative;
          padding: 1rem 0rem 2.5rem 0rem; } // reduced top and bottom padding by 50%

section { padding-top: 1rem;
          padding-bottom: 1rem; }

ul {
  list-style-type: circle;
}

ul,ol { width: inherit;
     -webkit-padding-start: 0%;
     -webkit-padding-end: 0%;
     list-style-position: inside;
     } //list-style-type: none; 

//li { padding: 0.5em 0; } //vertical padding on list items screws up vertical rhythym

figure, figure img.maincolumn { max-width: 55%;
         -webkit-margin-start: 0;
         -webkit-margin-end: 0;
         margin-top: 3em; }

figcaption { float: right;
             clear: right;
             margin-right: -48%;
             margin-top: 0;
             margin-bottom: 0;
             font-size: 1.0rem;
             line-height: 1.6;
             vertical-align: baseline;
             position: relative;
             max-width: 40%; }

figure.fullwidth figcaption { float: left; margin-right: 0%; margin-left: 36%; }

img { max-width: 100%; }

.sidenote, .marginnote { float: right;
                         clear: right;
                         margin-right: -60%;
                         width: 50%;
                         margin-top: 0;
                         margin-bottom: 1.96rem;
                         font-size: 1.0rem;
                         line-height: 1.96; //changed to bring line heights into rational pattern
                         vertical-align: baseline;
                         position: relative; }

li .sidenote, li .marginnote{ margin-right: -80%; } //added to allow for the fact that lists are indented and marginnotes and sidenotes push to right

blockquote .sidenote, blockquote .marginnote { margin-right: -60% } // modified by MountAye

.sidenote-number { counter-increment: sidenote-counter; }

.sidenote-number:after, .sidenote:before { content: counter(sidenote-counter) " ";
                                           font-family: et-bembo-roman-old-style;
                                           color: $contrast-color; //added color
                                           position: relative;
                                           vertical-align: baseline; }

.sidenote-number:after { content: counter(sidenote-counter);
                         font-size: 1rem;
                         top: -0.5rem;
                         left: 0.1rem; }

.sidenote:before { content: counter(sidenote-counter) ".\000a0"; // this is unicode for a non-breaking space
                   color: $contrast-color;
                   top: 0rem; } //removed superscripting for numerical reference in sidenote

div.table-wrapper { overflow-x: auto; } //changed all overflow values to 'auto' so scroll bars appear only as needed

@media screen and (max-width: 760px) { p, footer, div.mathblock, blockquote { width: 90%; }
                                       pre code { width: 87.5%; }
                                       ul { width: 85%; }
                                       figure { max-width: 90%; }
                                       figcaption, figure.fullwidth figcaption { margin-right: 0%;
                                                                                 max-width: none; }
                                       blockquote p, blockquote footer {  width: 90%;
                                                                          padding-left: 5%;
                                                                          padding-right: 5%;
                                                                        }
                                       .ghComment, .ghBody, .ghFooter { margin-left: 0; } }

@media (max-width: 760px) { label.margin-toggle:not(.sidenote-number) { display: inline; color: $contrast-color; }
                            .sidenote, .marginnote { display: none; }
                            .margin-toggle:checked + .sidenote,
                            .margin-toggle:checked + .marginnote { display: block;
                                                                   float: left;
                                                                   left: 1rem;
                                                                   clear: both;
                                                                   width: 95%;
                                                                   margin: 1rem 2.5%;
                                                                   vertical-align: baseline;
                                                                   position: relative; }
                            label { cursor: pointer; }
                            pre, pre code, p code, p pre code { width: 90%;
                                                                padding: 0; }
                            .table-caption { display: block;
                                             float: right;
                                             clear: both;
                                             width: 98%;
                                             margin-top: 1rem;
                                             margin-bottom: 0.5rem;
                                             margin-left: 1%;
                                             margin-right: 1%;
                                             vertical-align: baseline;
                                             position: relative; }
                            div.table-wrapper, table, table.booktabs { width: 85%; }
                            div.table-wrapper { border-right: 1px solid #efefef; }
                            img { max-width: 100%; } }

.marginnote code, .sidenote code { font-size: 1rem; } //more .code class removal

pre, pre code, p pre code { width: inherit;
           padding-left: 2.5%;
           overflow-x: auto; }

.fullwidth, li.listing div{ max-width: 90%; }

.full-width { .sidenote, .sidenote-number, .marginnote { display: none; } }

span.newthought { font-variant: small-caps;
                  font-size: 1.2em;
                  letter-spacing: 0.05rem; }

input.margin-toggle { display: none; }

label.sidenote-number { display: inline; }

label.margin-toggle:not(.sidenote-number) { display: none; }

/*--- End of Basic Layout stuff from tufte.css ---*/

/* -- Jekyll specific styling --*/
//helper classes

.contrast { color: $contrast-color;}
.smaller { font-size: 80%;}
//Nav and Footer styling area

header > nav.group, body footer {
  width: 95%;
  padding-top: 2rem;
}

nav.group a.active:before{ content:"\0003c";} // escaped unicode for the carats and then a space on active menu links
nav.group a.active:after{ content:"\0003e" ;}

header > nav  a{
  font-size: 1.2rem;
  font-family: $sans-font;
  letter-spacing: 0.15em;
  text-transform: uppercase;
  color: $contrast-color;
  padding-top: 1.5rem;
  text-decoration: none;
  display: inline-block;
  float: left;
  margin-top: 0;
  margin-bottom: 0;
  padding-right: 2rem;
  //margin-left: 1rem;
  vertical-align: baseline;
}
header > nav a img{
  height: 5rem;
  position: relative;
  max-width: 100%;
  top:-1.5rem;
}
ul.footer-links, .credits{
  list-style: none;
  text-align: center;
  margin: 0 auto;
}
ul.footer-links li{
  display: inline;
  padding: 0.5rem 0.25rem;
}
.credits{
  padding: 1rem 0rem;
}

//change font color for credit links in footer

.credits{
  font-family: $sans-font;
    & a{
      color: $contrast-color;
    }
}

// End of Nav and Footer styling area

//Full width page styling stuff

article.full-width, .content-listing, ul.content-listing li.listing {
    width: 90%; 
    margin-left: auto;
    margin-right: auto;
    padding: 0% 5%;
}
.full-width  p{
  width: 90%;
}


h1.content-listing-header{
  font-style: normal;
  text-transform: uppercase;
  letter-spacing: 0.2rem;
  font-size: 1.8rem;
}

li.listing hr{
  width:100%;
}
.listing, .listing h3
{
  display: inline-block;
  margin:0;
}
li.listing {
  margin:0;
  & p{
    width: 100%
  }
}


li.listing:last-of-type{
  border-bottom: none;
  margin-bottom: 1.4rem;
}
li.listing h3.new {
  text-transform: uppercase;
  font-style: normal;
}
hr.slender {
    border: 0;
    height: 1px;
    margin-top: 1.4rem;
    margin-bottom:1.4rem;
    background-image: -webkit-linear-gradient(left, rgba(0,0,0,0), rgba(0,0,0,0.75), rgba(0,0,0,0));
    background-image:    -moz-linear-gradient(left, rgba(0,0,0,0), rgba(0,0,0,0.75), rgba(0,0,0,0));
    background-image:     -ms-linear-gradient(left, rgba(0,0,0,0), rgba(0,0,0,0.75), rgba(0,0,0,0));
    background-image:      -o-linear-gradient(left, rgba(0,0,0,0), rgba(0,0,0,0.75), rgba(0,0,0,0));
}
// End of front listing page stuff


// Printing ands screen media queries

// Does not display a print-footer for screen display
@media screen{
  .print-footer{
    display: none;
  }
}

//printing stuff
@media print {

    * {
    -webkit-transition: none !important;
    transition: none !important;
    }
    *,
    *:before,
    *:after {
        background: transparent !important;
        box-shadow: none !important;
        text-shadow: none !important;
    }
    @page {
        margin: 0.75in 0.5in 0.75in 0.5in;
        orphans:4; widows:2;
    }

    body {
        font-size:  12pt;

    }
    html body span.print-footer{
      font-family: $sans-font;
      font-size: 9pt;
      margin-top: 22.4pt;
      padding-top: 4pt;
      border-top: 1px solid #000;
    }

    thead {
        display: table-header-group;
    }

    tr,
    img {
        page-break-inside: avoid;
    }

    img {
        max-width: 100% !important;
    }

    p,
    h2,
    h3 {
        orphans: 4;
        widows: 4;
    }
    article h2, article h2 h3, article h3, article h3 h4, article h4, article h4 h5 {
        page-break-after: avoid;
    }

    body header , body footer {
      display:none;
    }
}

